import React from 'react';
import styled from 'styled-components';
import { Modal } from 'react-bootstrap';
import ModalComponent from './modal';
import { graphql, useStaticQuery } from 'gatsby';

const BackgroundWrapper = styled.div`
  background-image: url(${props => props.bg});
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: 0px 32px 102px rgba(23, 9, 40, 0.36);
  border-radius: 16px;
`;

const Container = styled.div`
  @media screen and (min-width: 1600px) {
    padding: 0rem 4rem 4rem 4rem;
  }
`;

export default ({ show, setShow, text }) => {
  const data = useStaticQuery(graphql`
    query TextForm {
      bg: file(relativePath: {eq: "home/form-background.png"}) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            src
          }
        }
      }
    }
  `);

  return (
    <>
      <ModalComponent
        show={show}
        setShow={setShow}
        dialogClassName="form-dialog"
      >
        <BackgroundWrapper bg={data.bg.childImageSharp.fluid.src}>
          <Modal.Header>
            <Modal.Title id="custom-modal-styling-title" className='w-100'>
              <div className='row w-100 d-flex justify-content-end'>
                <span className='icon popup-close' onClick={() => { setShow(false) }}></span>
              </div>
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Container className='container'>
              <p>Внимание!</p>
              <h2 className='mb-4'>Фалшива кампания с награди</h2>
              <p>{text}</p>
            </Container>
          </Modal.Body>
        </BackgroundWrapper>
      </ModalComponent>
    </>
  )
}

export const validateField = (field, value) => {
  switch (field) {
    case 'name':
    case 'firstName':
    case 'secondName':
    case 'city':
    case 'address':
      return value.length >= 3;
    case 'message':
      return value.length >= 1;
    case 'date':
      return value != null;
    case 'telephone':
      return isValidTelephone(value);
    case 'email':
      return isValidMail(value);
  }
}

const isValidTelephone = (phone) => {
  const r = /^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\s\./0-9]*$/;
  return r.test(phone);
}

const isValidMail = (email) => {
  const r = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return r.test(String(email).toLowerCase());
}

